header {
  border-top: 3px solid #7B4A2A;
  padding: 44px 0 30px;
  background-color: #fff;
}
header .header-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header .logo {
  margin-top: -14px;
}
header .navbar {
  padding: 0;
}
header .nav li {
  margin-right: 30px;
}
header .nav li:last-child {
  margin-right: 0;
}
header .nav a {
  font-weight: 500;
}
header .mail-icon {
  font-size: 14px;
}
header .phone-icon {
  font-size: 12px;
}
header .contacts {
  display: flex;
}
header .contacts a {
  display: flex;
  align-items: center;
  margin-right: 30px;
}
header .contacts a:last-child {
  margin-right: 0;
}
header .contacts a i {
  margin-right: 10px;
}
header .burger {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  padding: 0;
  background-color: transparent;
  border: none;
  font-size: 24px;
  outline: none;
  z-index: 50;
}

@media (min-width: 768px) {
  header .burger {
    display: none;
  }
}
@media (max-width: 991px) {
  header {
    font-size: 14px;
  }
  header .contacts {
    flex-direction: column;
  }
}
@media (max-width: 767px) {
  header {
    padding: 20px 0;
  }
  header .logo {
    margin-top: 0;
    margin-bottom: 10px;
  }
  header .navbar {
    margin-bottom: 10px;
  }
  header .nav {
    flex-direction: column;
    text-align: center;
  }
  header .nav li {
    margin-right: 0;
    margin-bottom: 5px;
  }
  header .nav li:last-child {
    margin-bottom: 0;
  }
  header .header-wrap {
    flex-direction: column;
  }
  header .contacts {
    align-items: center;
  }
  header .contacts a {
    margin-right: 0;
    margin-bottom: 5px;
  }
  header .contacts a:last-child {
    margin-bottom: 0;
  }
  header .burger {
    position: absolute;
    top: 15px;
    right: 15px;
  }
}
footer .footer {
  padding: 74px 0 60px;
  background-color: #F7F3E8;
}
footer .footer .footer-logo {
  margin-top: -14px;
}
footer .footer .footer-wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
footer .footer .contacts-wrap {
  flex: 1 1;
  display: flex;
  justify-content: space-evenly;
}
footer .footer .contact-item {
  display: flex;
  align-items: center;
}
footer .footer .contact-icon {
  margin-right: 10px;
}
footer .footer .email-icon,
footer .footer .address-icon {
  font-size: 14px;
}
footer .footer .phone-icon {
  font-size: 12px;
}
footer .footer .social-link {
  font-size: 20px;
  margin-right: 10px;
}
footer .footer .social-link:last-child {
  margin-right: 0;
}
footer .project-by {
  padding: 50px 0 40px;
  display: flex;
  justify-content: center;
}
footer .project-by .studio {
  text-align: center;
}
footer .project-by .studio:hover .studio-logo path {
  fill: #414141;
}
footer .project-by .studio:hover .studio-name {
  color: #414141;
}
footer .project-by .studio-logo {
  margin-bottom: 20px;
}
footer .project-by .studio-logo path {
  transition: all 0.2s ease-in-out;
}
footer .project-by .studio-name {
  transition: all 0.2s ease-in-out;
  font-family: "Roboto", sans-serif;
  color: #c4c4c4;
}

@media (max-width: 991px) {
  footer .footer .contacts-wrap {
    flex-direction: column;
    align-items: center;
  }
}
@media (max-width: 575px) {
  footer .footer {
    padding: 50px 0;
  }
  footer .footer .footer-wrap {
    flex-direction: column;
  }
  footer .footer .footer-logo {
    margin-top: 0;
    margin-bottom: 10px;
  }
  footer .footer .contacts-wrap {
    margin-bottom: 10px;
  }
  footer .footer .contact-item {
    margin-bottom: 5px;
  }
}
html, body {
  height: 100%;
}

body {
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #414141;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1 1;
}

.modal-dialog {
  margin: 12rem auto;
}

.inner-page {
  padding-top: 50px;
}
.inner-page .h1 {
  margin-bottom: 50px;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  color: #414141;
  transition: all 0.2s ease-in-out;
}
a:hover {
  color: #3B7A17;
  text-decoration: none;
}

.h1 {
  font-size: 36px;
  text-transform: uppercase;
  font-weight: 500;
  color: #3B7A17;
}

.c-btn {
  display: inline-block;
  padding: 8px 16px;
  color: #fff;
  font-size: 14px;
  border-radius: 30px;
  background-color: #7C5131;
  transition: all 0.2s ease-in-out;
  border: none;
}
.c-btn:focus {
  outline: none;
}

.c-btn:hover {
  background-color: #6A4C36;
}

.c-btn.disabled {
  opacity: 0.5;
}

.c-btn.disabled:hover {
  background-color: #7C5131;
}

.spacer {
  flex-grow: 1;
}

.form-group {
  position: relative;
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
}

.form-control {
  max-width: 330px;
  padding: 10px;
  height: 40px;
  flex: 1 1 auto;
  border: 1px solid #7A8C72;
  border-radius: 6px;
  outline: none;
  font-size: 14px;
}
.form-control:focus {
  box-shadow: none;
  border-color: #7A8C72;
}

textarea.form-control {
  height: auto;
}

.form-label {
  position: relative;
  margin: 8px 0;
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
}
.form-label .required {
  color: red;
}

@media (max-width: 1199px) {
  .h1 {
    font-size: 26px;
  }
}
@media (max-width: 767px) {
  .inner-page {
    padding-top: 30px;
  }
  .inner-page .h1 {
    margin-bottom: 30px;
  }
}
@media (max-width: 575px) {
  .form-group {
    flex-direction: column;
  }

  .form-control {
    max-width: 100%;
  }
}